import Editor from 'for-editor';
import question from '@/api/question'
import React, { Component } from 'react';
import { Layout, message, Breadcrumb, Input, Select, Button } from 'antd';

const { Option } = Select;
const { Content } = Layout;

class AddQuestions extends Component {
    state = {
        collapsed: false,
        iptval: '',
        topic: '',
        answer: '',
        examtype: [],
        subjecttype: [],
        topictype: [],
        select_exam: '',
        select_subject: '',
        select_topic: '',
        username: ''
    };
    onCollapse = collapsed => {
        this.setState({ collapsed });
    };
    render() {
        let {
            iptval,
            topic,
            answer,
            examtype,
            subjecttype,
            topictype
        } = this.state;
        return <div>
            <Layout
                style={{
                    padding: '0 24px 24px',
                    height: 'auto',
                    width: '1000px'
                }}>
                <Breadcrumb style={{ margin: '16px 0' }}>
                    <Breadcrumb.Item></Breadcrumb.Item>
                </Breadcrumb>
                <Content
                    style={{
                        background: '#fff',
                        padding: 24,
                        margin: 0,
                        minHeight: 300
                    }}>
                    <div></div>
                    <div style={{ margin: '20px 0' }}></div>
                    <Input
                        placeholder='20'
                        value={iptval}
                        onChange={e => {
                            this.setState({
                                iptval: e.target.value
                            });
                        }}
                    />
                    <div style={{ margin: '20px 0' }}> </div>
                    <Editor
                        style={{ height: '300px' }}
                        value={topic}
                        onChange={e => {
                            this.setState({
                                topic: e
                            });
                        }}
                    />
                    <div style={{ margin: '20px 0' }}>:</div>
                    <Select
                        defaultValue=''
                        style={{ width: 120 }}
                        onChange={e => {
                            this.setState({
                                select_exam: e
                            });
                        }}>
                        {examtype && examtype.map((item, index) => (
                            <Option value={item.text} key={index}>
                                {item.text}
                            </Option>
                        ))}
                    </Select>
                    <div style={{ margin: '20px 0' }}></div>
                    <Select
                        defaultValue=''
                        style={{ width: 120 }}
                        onChange={e => {
                            this.setState({
                                select_subject: e
                            });
                        }}>
                        {subjecttype &&
                            subjecttype.map((item, index) => (
                                <Option value={item.subject_name} key={index}>
                                    {item.subject_name}
                                </Option>
                            ))}
                    </Select>
                    <div style={{ margin: '20px 0' }}>:</div>
                    <Select
                        defaultValue=''
                        style={{ width: 120 }}
                        onChange={e => {
                            this.setState({
                                select_topic: e
                            });
                        }}>
                        {topictype &&
                            topictype.map((item, index) => (
                                <Option value={item.typename} key={item.id}>
                                    {item.typename}
                                </Option>
                            ))}
                    </Select>
                    <div style={{ margin: '20px 0' }}></div>
                    <Editor
                        style={{ height: '300px' }}
                        value={answer}
                        onChange={e => {
                            this.setState({
                                answer: e
                            });
                        }}
                    />
                    <Button type='primary' onClick={this.submit}>
                        
                        </Button>
                </Content>
            </Layout>
        </div>
    }
    /**
    * 
    */
    submit = () => {
        let { iptval, topic, answer, select_exam, select_subject, select_topic, username } = this.state;
        username = window.sessionStorage.getItem('name')
        console.log(username)
        question.addquestion({ iptval, topic, answer, select_exam, select_subject, select_topic, username }).then(res => {
            let { code, msg } = res.data;
            if (code) {
                message.success(msg)
            }
            else {
                message.error(msg);
            }
        })
    }
    componentDidMount() {
        /**
         * 
         */
        question.exam_type().then(res => {
            this.setState({
                examtype: res.data.result
            })
        })
        /**
         * 
         */
        question.subject_type().then(res => {
            this.setState({
                subjecttype: res.data.result
            })
        })
        /**
         * 
         */
        question.topic_type().then(res => {
            this.setState({
                topictype: res.data.result
            })
        })
    }
}

export default AddQuestions;
