import 'antd/dist/antd.css';
import '@/assets/css/exam/exam.css';
import moment from 'moment';
import 'moment/locale/zh-cn';
import React, { Component } from 'react';
import { Select, Button, Table, message } from 'antd';
import { getTestList, getTestClass, getTestType, getDetailTest } from '@/api/exam';

moment.locale('zh-cn');
const { Option } = Select;


class Exam extends Component {
    state = {
        testType: '',
        testObject: '',
        data: [],
        allList: [],
        contList: [
            {
                id: 0,
                cont: ''
            },
            {
                id: 1,
                cont: ''
            },
            {
                id: 2,
                cont: ''
            }
        ],
        columns: [
            {
                title: '',
                dataIndex: 'info',
                key: 'info',
            },
            {
                title: '',
                dataIndex: 'classtest',
                key: 'classtest',
            },
            {
                title: '',
                dataIndex: 'createman',
                key: 'createman',
            },
            {
                title: '',
                dataIndex: 'startDate',
                key: 'startDate',
            },
            {
                title: '',
                dataIndex: 'endtDate',
                key: 'endtDate',
            },
            {
                title: '',
                key: 'action',
                render: (text, record) => {
                    return <span>
                        <b onClick={() => this.handleDetail(record)}></b>
                    </span>
                }
            }
        ],
        ind: 0
    };
    handleChange = (value) => {
        this.setState({
            testType: value
        })
    }
    handleObject = (value) => {
        this.setState({
            testObject: value
        })
    }
    handleSearch() {
        getTestType({
            testType: this.state.testType,
            testObject: this.state.testObject
        }).then(res => {
            const dataList = res.data.result.map(item => {
                item.startDate = moment(+item.startDate).format("YYYY-MM-DD HH:mm:ss");
                item.endtDate = moment(+item.endtDate).format("YYYY-MM-DD HH:mm:ss");
                return item;
            })
            this.setState({
                data: dataList
            })
        })
    }
    handleDetail(record) {
        getDetailTest({
            examtype: record.examtype,
            course: record.course
        }).then(res => {
            if (res.data.code) {
                const result_cont = res.data.result.slice(0, record.cont);
                const url = `/main/detail/${record.key}`;
                this.props.history.push({ pathname: url, state: { data: result_cont } })
                message.success(`${res.data.msg}`);
            } else {
                message.success(`${res.data.msg}`);
            }
        }).catch(error => {
            message.success(error);
        })
    }
    render() {
        const { testType, testObject, data, columns, contList, ind } = this.state;
        return (
            <div className="Exam">
                <h2 className="top"></h2>
                <div className="center">
                    <span className="spans"></span>
                    <Select defaultValue="" style={{ width: 120 }} value={testType} onChange={this.handleChange}>
                        <Option value=""></Option>
                        <Option value=""></Option>
                        <Option value=""></Option>
                        <Option value=""></Option>
                    </Select>
                    <span className="spans"></span>
                    <Select defaultValue="javaScript" style={{ width: 120 }} value={testObject} onChange={this.handleObject}>
                        <Option value="javaScript">javaScript</Option>
                        <Option value="javaScript">javaScript</Option>
                        <Option value=""></Option>
                        <Option value=""></Option>
                        <Option value="node">node</Option>
                        <Option value="(vue)">(vue)</Option>
                        <Option value="(react)">(react)</Option>
                        <Option value=""></Option>
                        <Option value="javaScript">javaScript</Option>
                        <Option value="node">node</Option>
                    </Select>
                    <Button type="primary" icon="search" onClick={() => this.handleSearch()} className="exam-search">
                        
              </Button>
                </div>
                <div className="bottom">
                    <p className="bottom-top">
                        <span></span>
                        {
                            contList.map(item => (
                                <b key={item.id} className={item.id === ind ? 'active' : ''} onClick={() => this.handleClick(item.id, item)}>{item.cont}</b>
                            ))
                        }
                    </p>
                    <div className="bottom-bottom">
                        <Table columns={columns} dataSource={data} />
                    </div>
                </div>
            </div>
        );
    }
    componentDidMount() {
        this._getTestList();

    }

    _getTestList() {
        getTestList().then(res => {
            const dataList = res.data.result.map(item => {
                item.startDate = moment(+item.startDate).format("YYYY-MM-DD HH:mm:ss");
                item.endtDate = moment(+item.endtDate).format("YYYY-MM-DD HH:mm:ss");
                return item;
            })
            this.setState({
                data: dataList,
                allList: dataList
            })
        })
    }

    handleClick(id, obj) {

        this.setState({
            ind: id
        })
        getTestClass(id).then(res => {
            const dataList = res.data.result.map(item => {
                item.startDate = moment(+item.startDate).format("YYYY-MM-DD HH:mm:ss");
                item.endtDate = moment(+item.endtDate).format("YYYY-MM-DD HH:mm:ss");
                return item;
            })
            this.setState({
                data: dataList
            })
            if (this.state.ind === 0) {
                this.setState({
                    data: this.state.allList
                })
            }

        })


    }

}

export default Exam;
