import "@/assets/css/grade/student.css";
import { student } from '@/api/setClass';
import React, { Component } from "react";
import { Form, Input, Select, Button, Table, message, Popconfirm } from "antd";
import { serchStudent, delStudent, getClass, getRoomList } from '@/api/classManage';

const { Option } = Select;
@Form.create({ name: "horizontal_login" })
class Student extends Component {
  state = {
    list: [],
    rooms: [],
    grade: []
  };

  componentDidMount() {
    getClass().then(res => {
      this.setState({
        grade: res.data.result
      })
    })
    //room
    getRoomList().then(res => {
      this.setState({
        rooms: res.data.result
      });
    });
    this.props.form.validateFields();
    student().then(res => {
      this.setState({
        list: res.data.user
      });
    });
  }
  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      serchStudent(values).then(res => {
        this.setState({
          list: res.data.result
        })
      })
    });
  };
  confirm = (rec) => {
    console.log(rec)
    delStudent(rec).then(res => {
      student().then(res => {
        this.setState({
          list: res.data.user
        });
      });
    })
    message.success('');
  }

  cancel = (e) => {
    message.error('');
  }

  render() {
    const columns = [
      {
        title: "",
        dataIndex: "name",
        key: "name"
      },
      {
        title: "",
        dataIndex: "student_number",
        key: "student_number"
      },
      {
        title: "",
        dataIndex: "class_number",
        key: "class_number"
      },
      {
        title: "",
        dataIndex: "classroom",
        key: "classroom"
      },
      {
        title: "",
        dataIndex: "password",
        key: "password"
      },
      {
        title: "",
        dataIndex: "operation",
        key: "operation",
        render: (text, record) => (
          <span>
            <Popconfirm
              title="?"
              onConfirm={() => this.confirm(record)}
              onCancel={this.cancel}
              okText="Yes"
              cancelText="No"
            >
              <span className="cus" >{text}</span>
            </Popconfirm>

          </span>
        )
      }
    ];
    const { getFieldDecorator } = this.props.form;
    return (
      <div className="studentManage">
        <h3 className="h3"></h3>
        <div className="top">
          <Form layout="inline" onSubmit={this.handleSubmit} >
            <Form.Item>
              {getFieldDecorator("name", {
              })(<Input placeholder="" style={{ height: 32 }} />)
              }
            </Form.Item>
            <Form.Item className="wid">
              {getFieldDecorator("room", {

              })(
                <Select
                  className="select1"
                  placeholder=""
                  onChange={this.handleSelectChange}
                >
                  {
                    this.state.rooms.map((item, index) => {
                      return <Option value={item.room_name} key={item.room_id}>{item.room_name}</Option>
                    })
                  }
                </Select>
              )}
            </Form.Item>
            <Form.Item className="wid">
              {getFieldDecorator("banji", {

              })(<Select
                className="select1"
                placeholder=""
                onChange={this.handleSelectChange}
              >
                {
                  this.state.grade.map((item, index) => {
                    return <Option value={item.class} key={item.id}>{item.class}</Option>
                  })
                }
              </Select>)
              }
            </Form.Item>
            <Form.Item>
              <Button htmlType='submit' type="primary"></Button>
            </Form.Item>
            <Form.Item>
              <Button type="primary" onClick={() => {
                this.props.form.resetFields();
                student().then(res => {
                  this.setState({
                    list: res.data.user
                  });
                });
              }}></Button>
            </Form.Item>
          </Form>
        </div>
        <div className="button">
          <Table columns={columns} dataSource={this.state.list} />
        </div>
      </div>
    );
  }
}

export default Student;
